.title {
  /* padding-top: 32px; */
  padding: 24px 0;
  margin: 0 auto;
  text-align: center;
}

#status {
  color:red;
}


.tab-items {
  text-align: left;
  padding: 20px;
  margin: 0 auto;
}

.main-container {
  margin: 10px auto;
  width: 95%;
}

.full-block {
  width: 100%;
}
/* .App {
  text-align: center;
} */

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



.example-div {
  border: 1px solid gray;
  margin: 2px;
  padding: 5px;
  border-radius: 5px;
  font-size: x-small;
}

.nft-contract-textarea {
  width: 95%;
  padding: 10px;
  min-height: 200px;
  overflow: scroll;
}
